import { userLogin } from '../../apiCalls/callApi';
import { error } from '../../apiCalls/errorConstants';
import HttpStatus, { EXPECTATION_FAILED } from 'http-status-codes';
import mockAxios from 'axios';

describe("LoginBox Component", () => {


  it("calls axios getCall and returns users", async () => {
    mockAxios.post.mockImplementationOnce(() => Promise.resolve({
        response: {
            status: HttpStatus.OK
        }
    }));

    const login = userLogin({email: "abcd@123.com", password: "abc"}).then(response => {
        expect(mockAxios.get).toHaveBeenCalledTimes(1);
    }).catch(err => {});
    console.log(login);
    
  });

});
